@font-face {
    font-family: CloisterBlack;
    src: url(../../font/CloisterBlack.ttf);
}

.background {
    height: 100vh;
    background-image: none;
    background-color: rgb(30, 31, 34);
    background-image: url(../../image/StarrySky.png);
    background-size: cover;
}

.iconArea {
    max-width: 400px;
    margin: auto;
}

.nameText {
    font-family: CloisterBlack;
    font-size: 56px;
    color: white;
    width: 70%;
}

.toolBtn {
    height: 60px;
    width: 60px;
    padding: 10px;
    background-color: white;
    border-radius: 10px;
}

.contentArea {
    padding: 30px 100px 30px 100px;
    background-color: rgb(49, 51, 56);
    background-image: linear-gradient(rgb(49, 51, 70), rgb(29, 29, 30));
}

.titleArea {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.titleBox {
    height: 50px;
    width: 5px;
    margin-right: 10px;
    background-color: white;
}

.flip-card {
    background-color: transparent;
    height: 300px;
    width: 220px;
    perspective: 1000px;
    margin-bottom: 30px;
}
  
.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border: 10px solid white;
}
  
.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}
  
.flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
  
.flip-card-front {
    background-color: #505050;
    color: black;
}
  
.flip-card-back {
    background-color: #606060;
    color: white;
    transform: rotateY(180deg);
}