.articleBackground {
    background-image: url(../../image/StarrySky.png);
    background-size: cover;
    background-attachment: fixed;
}

.articleTopBox {
    padding: 100px 50px 0px 50px;
}

.articleContent {
    background-color: white;
    margin-top: 25px;
    padding: 25px 50px 25px 50px;
    border-radius: 25px;
}

.articleBottomBox {
    padding: 50px 50px 100px 50px;
}

.articleButton {
    margin: 50px 0px 50px 0px;
}

.articleInfo {
    top: 100px;
    position: sticky;
}

.editContent {
    height: 100vh;
    margin-top: 25px;
    margin-bottom: 25px;
    resize: none;
}

.editInput {
    font-size: 32px;
    margin-right: 25px;
}