@font-face {
    font-family: CloisterBlack;
    src: url(../../font/CloisterBlack.ttf);
}

.blogContentBox {
    margin-top: 75px;
    padding: 0px 120px 0px 75px;
    background-image: url(../../image/StarrySky.png);
    background-size: cover;
    background-attachment: fixed;
}

.blogContentArea {
    padding: 30px 0px 30px 35px;
    border-left: 25px solid white;
    min-height: calc(100vh - 75px);
}

.articleElementBox {
    height: 375px;
    background-color: white;
    border-radius: 20px;
}

.articleElementText {
    padding-left: 15px;
    color: black;
}

.articleElementImage {
    position: relative;
    height: 270px; 
    border-bottom: 2px solid gray;
    background-color: gray;
    background-size: cover;
    background-position: center;
    border-radius: 20px 20px 0px 0px;
}

.articleElementCategory {
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 20px 20px 0px 0px;
    background-color: rgba(64, 64, 64, 0.5);

    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;
}

.articleElementCategoryText {
    color: white;
    font-size: 24px;
    margin: 10px;
}

.blogToolbar {
    position: fixed;
    top: 0;
    height: 75px;
    width: 100%;

    display: flex; 
    flex-direction: row; 
    justify-content: space-between; 
    align-items: center;
}

.blogToolbarTitle {
    font-size: 30px;
    color: white;
    padding-left: 50px;
}

.blogToolbarButtonGroup {
    padding-right: 50px;

    display: flex; 
    flex-direction: row; 
    justify-content: flex-end; 
    align-items: center;
}

.blogToolbarButton {
    color: white;
    font-size: 24px;
}
